@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Black.woff2") format("woff2"), url("../fonts/Avenir-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal; }

@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Heavy.woff2") format("woff2"), url("../fonts/Avenir-Heavy.woff") format("woff");
  font-weight: 800;
  font-style: normal; }

@font-face {
  font-family: "Avenir";
  src: url("../fonts/Avenir-Medium.woff2") format("woff2"), url("../fonts/Avenir-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  font-family: "Avenir", "Arial", sans-serif;
  box-sizing: border-box;
  min-width: 320px;
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.header {
  background-color: #161851;
  background-attachment: fixed;
  height: 100%; }

.header__wrap {
  padding: 30px 5%;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__menu {
  width: 45%;
  max-width: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__contact {
  display: flex;
  width: 45%;
  max-width: 200px;
  justify-content: space-between;
  align-items: baseline;
  flex-shrink: 1; }

@media (min-width: 768px) {
  .header__menu {
    max-width: 250px;
    align-items: center; }
  .header__contact {
    max-width: 450px; } }

@media (min-width: 1300px) {
  .header__menu {
    max-width: 270px; }
  .header__contact {
    max-width: 600px; }
  .header__wrap {
    max-width: 1300px;
    margin: 0 auto; } }

.contact__social-icon {
  align-items: center;
  justify-content: space-between;
  width: 33%;
  max-width: 150px;
  display: none; }

.contact__telephone {
  font-weight: 500;
  font-size: 12px;
  color: #ffffff; }

@media (min-width: 768px) {
  .contact__social-icon {
    display: flex; }
  .contact__telephone {
    font-size: 16px; } }

@media (min-width: 1300px) {
  .contact__telephone {
    font-size: 18px; } }

@media (min-width: 768px) {
  .menu__logo {
    width: 172px; }
  .menu__hamburger {
    width: 52px;
    height: 42px; } }

.evision {
  background-color: #161851;
  /*background-attachment: fixed;
  height: 100%;
  position: relative;*/
  position: relative;
  height: 100vh;
  background-attachment: fixed; }
  .evision::after {
    position: absolute;
    top: 0;
    left: 50%;
    background-image: url("../img/circle.png");
    width: 744px;
    height: 557px;
    background-color: #1b1d60;
    opacity: 0.3;
    background-repeat: no-repeat;
    background-position: center bottom;
    margin-left: -372px; }

.evision__smart {
  font-weight: 900;
  color: #ffffff;
  text-align: center;
  font-size: 26px;
  line-height: 32px;
  margin: 0;
  padding-top: 40px; }

.evision__list {
  margin-top: 55px;
